<template>
    <div class="container">
        <div class="main__cars" v-if="users">
            <div class="main__cars-item" v-for="user in users" :key="user.id">
                <p>Name: {{ user.name }}</p>
                <p>Email: {{ user.email }}</p>
                <p>City: {{ user.address.city }}</p>
            </div>
        </div>
        <div class="main__section" v-else>
            Идет загрузка...
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                users: null
            }
        },
        created() {
            this.getUsers()
        },
        methods: {
            async getUsers() {
                let response = await fetch('https://jsonplaceholder.typicode.com/users')
                let data = await response.json()
               this.users = data
            }
        },
    }
</script>
