import { createRouter, createWebHistory } from 'vue-router'


import Home from '@/pages/Home.vue'


export const routers = createRouter({
    history: createWebHistory(),
    routes: [
        { path: '/', name: 'HomePage', component: Home },
        { path: '/Todos', name: 'Todos', component: () => import('@/pages/Todos.vue')},
        { path: '/Posts', name: 'Posts', component: () => import('@/pages/Posts.vue')},
        { path: '/Pictures', name: 'Pictures', component: () => import('@/pages/Pictures.vue')},
        { path: '/posts/:id', name: 'CarItem', component: () => import('@/pages/CarItem.vue')},
    
    ]
})